import { Button, Grid, Heading, Text, useToast, VStack } from '@chakra-ui/react';
import React from 'react';
import { useForm } from 'react-hook-form';
import { CheckingGridItems } from './CheckingGridItems';
import { DebitCardGridItems } from './DebitCardGridItems';
import { GenericGridItems } from './GenericGridItems';
import { pick } from 'lodash';

const templateAreas = {
  CHECKING: {
    base: `
"accountNumber"
"accountType"
"routingNumber"
"bankAccountNumber"
"confirmBankAccountNumber"
"check"
`,
    lg: `
"accountNumber accountNumber accountNumber"
"accountType accountType check"
"routingNumber routingNumber check"
"bankAccountNumber bankAccountNumber check" 
"confirmBankAccountNumber confirmBankAccountNumber check"
`,
  },
  DEBIT_CARD: {
    base: `
"accountNumber"
"accountType"
"cardNumber"
"nameOnCard"
"expirationDate"
"cvv"
"creditCard"
`,
    lg: `
"accountNumber accountNumber accountNumber"
"accountType accountType creditCard"
"cardNumber cardNumber creditCard"
"nameOnCard nameOnCard creditCard"
"expirationDate cvv creditCard"
`,
  },
};

const fields = {
  CHECKING: ['routingNumber', 'bankAccountNumber'],
  DEBIT_CARD: ['accountType', 'cardNumber', 'nameOnCard', 'expirationDate', 'cvv'],
};

export function OneTimeLoanPaymentPage() {
  const { control, handleSubmit, watch } = useForm({
    defaultValues: {
      accountNumber: '',
      accountType: 'CHECKING',
      cardNumber: '',
      cvv: '',
      expirationDate: '',
      nameOnCard: '',
    },
  });
  const accountType = watch('accountType');
  const toast = useToast();
  return (
    <VStack align="flex-start" py="3rem" px={{ base: 4, lg: '6rem' }} spacing={6} w="100%">
      <VStack align="flex-start" w="100%">
        <Heading fontSize="2rem">One-time Loan Payment</Heading>
        <Text>Fill out the form below to complete your payment.</Text>
      </VStack>
      <VStack
        as="form"
        align="flex-start"
        spacing={6}
        onSubmit={handleSubmit(values => {
          console.log(JSON.stringify(pick(values, ['accountNumber', 'accountType', ...fields[accountType]]), null, 2));

          toast({
            isClosable: true,
            colorScheme: 'teal',
            description: 'Your payment was successful. See browser console for details.',
            title: 'Success',
          });
        })}
        w="100%"
      >
        <Grid
          bg="gray.300"
          border={1}
          borderColor="gray.300"
          borderStyle="solid"
          gap="1px"
          templateAreas={templateAreas[accountType]}
          templateColumns={{ base: '1fr', lg: '1fr 1fr 23rem' }}
          w={{ base: '100%', lg: '45rem' }}
        >
          <GenericGridItems control={control} />
          {
            {
              CHECKING: <CheckingGridItems control={control} />,
              DEBIT_CARD: <DebitCardGridItems control={control} />,
            }[accountType]
          }
        </Grid>
        <Button
          colorScheme="teal"
          px="4rem"
          py="1.5rem"
          textTransform="uppercase"
          type="submit"
          variant="solid"
          w={{ base: '100%', lg: 'initial' }}
        >
          Make Payment
        </Button>
      </VStack>
    </VStack>
  );
}
