import { FormControl, FormErrorMessage, FormLabel, GridItem, Text, VStack } from '@chakra-ui/react';
import React from 'react';
import { Controller, useFormState } from 'react-hook-form';
import { get } from 'lodash';

export function PaymentFieldGridItem({ control, label, name, render, rules }) {
  const formState = useFormState({ control, name });
  const message = get(formState, ['errors', name, 'message']);
  return (
    <GridItem area={name} bg="white">
      <FormControl isInvalid={!!message}>
        <VStack align="flex-start" pb={2} px={2} pt={1} spacing={0}>
          <FormLabel color={message ? 'red.500' : 'gray.600'}>{label}:</FormLabel>
          <Controller
            control={control}
            name={name}
            render={render}
            rules={{ required: { value: true, message: `${label} is required` }, ...rules }}
          />
        </VStack>
        <FormErrorMessage bg="red.100" borderTop={1} borderColor="gray.300" borderStyle="solid" px={2} py={1}>
          <Text color="inherit" fontSize="0.75rem" wordBreak="break-all" noOfLines={1}>
            {message}
          </Text>
        </FormErrorMessage>
      </FormControl>
    </GridItem>
  );
}
