import { Input } from '@chakra-ui/react';
import { DateTime } from 'luxon';
import React from 'react';
import { NumericFormat, PatternFormat } from 'react-number-format';
import { PaymentFieldGridItem } from './PaymentFieldGridItem';
import { WhereCanIFindGridItem } from './WhereCanIFindGridItem';

export function DebitCardGridItems({ control }) {
  return (
    <>
      <PaymentFieldGridItem
        name="cardNumber"
        label="Card Number"
        control={control}
        render={({ field }) => <Input as={NumericFormat} {...field} />}
      />
      <PaymentFieldGridItem
        name="nameOnCard"
        label="Name On Card"
        control={control}
        render={({ field }) => <Input {...field} />}
      />
      <PaymentFieldGridItem
        name="expirationDate"
        label="Expiration Date"
        control={control}
        render={({ field }) => <Input as={PatternFormat} format="##/##" mask="_" placeholder="MM/YY" {...field} />}
        rules={{
          validate: {
            isValid: v => (DateTime.fromFormat(v, 'LL/yy').isValid ? null : 'Invalid Expiration Date'),
            isLater: v => (DateTime.fromFormat(v, 'LL/yy') > DateTime.now() ? null : 'Invalid Expiration Date'),
          },
        }}
      />
      <PaymentFieldGridItem
        name="cvv"
        label="CVV"
        control={control}
        render={({ field }) => <Input as={PatternFormat} format="###" placeholder="###" {...field} />}
        rules={{ validate: v => (v.length === 3 ? null : 'Invalid CVV') }}
      />
      <WhereCanIFindGridItem area="creditCard" src="/cvv.png">
        Where can I find the CVV number?
      </WhereCanIFindGridItem>
    </>
  );
}
