import { HStack, Input, Radio, RadioGroup } from '@chakra-ui/react';
import React from 'react';
import { NumericFormat } from 'react-number-format';
import { PaymentFieldGridItem } from './PaymentFieldGridItem';

export function GenericGridItems({ control }) {
  return (
    <>
      <PaymentFieldGridItem
        name="accountNumber"
        label="Loan Account Number"
        control={control}
        render={({ field }) => <Input as={NumericFormat} {...field} />}
      />
      <PaymentFieldGridItem
        name="accountType"
        label="Type of Account"
        control={control}
        render={({ field }) => (
          <RadioGroup {...field}>
            <HStack spacing={10}>
              <Radio value="CHECKING">Checking</Radio>
              <Radio value="DEBIT_CARD">Debit Card</Radio>
            </HStack>
          </RadioGroup>
        )}
      />
    </>
  );
}
