import { Input } from '@chakra-ui/react';
import { trim } from 'lodash';
import React from 'react';
import { NumericFormat, PatternFormat } from 'react-number-format';
import { PaymentFieldGridItem } from './PaymentFieldGridItem';
import { WhereCanIFindGridItem } from './WhereCanIFindGridItem';

export function CheckingGridItems({ control }) {
  return (
    <>
      <PaymentFieldGridItem
        name="routingNumber"
        label="Routing Number"
        control={control}
        render={({ field }) => (
          <Input as={PatternFormat} format="#########" mask="#" placeholder="#########" {...field} />
        )}
      />
      <PaymentFieldGridItem
        name="bankAccountNumber"
        label="Bank Account Number"
        control={control}
        render={({ field }) => <Input as={NumericFormat} {...field} />}
      />
      <PaymentFieldGridItem
        name="confirmBankAccountNumber"
        label="Confirm Bank Account Number"
        control={control}
        render={({ field }) => <Input as={NumericFormat} {...field} />}
        rules={{
          validate: (v, { bankAccountNumber }) =>
            trim(v) === trim(bankAccountNumber) ? null : 'Must match Bank Account Number',
        }}
      />
      <WhereCanIFindGridItem area="check" src="/check.png">
        Where can I find the routing and account number?
      </WhereCanIFindGridItem>
    </>
  );
}
